div#Inicio{

    width: 100%;
    height: 500px;
    padding-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
#presentacion{
    background-color: var(--color-complementario);
    width: 100%;
    height: 400px;
    border-top: 10px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
}
#presentacion h1{
    text-decoration: underline;
    margin:10px;
}
#presentacion p{
    margin:0 100px;
    border-top: 10px;
    font-size: 100%;

}